import React, { Component } from 'react';
import { Link } from "react-router-dom";
import style from "./style.scss";
import { MDBBtn } from "mdbreact";


class Follower extends Component {
  render() {
    const { follower } = this.props;
    return (
      <div className={style.card}>
        <div className={style.cardAvatar}>
          <img src={follower.imgUrl} alt={follower.name}/>        
        </div>
        <Link className={style.cardName} to={`/${follower.username}`}>
          {follower.name}
        </Link>
        <div 
          className={style.cardPosition}
          style={{
            overflow: 'hidden',
            textOverflow: 'ellipsis',
            WebkitBoxOrient: 'vertical',
            display: '-webkit-box',
            WebkitLineClamp: 2}}>
            {follower.position}
        </div>
        <div className={style.cardStat}>
          {follower.follower} followers | {follower.post} post
        </div>
        <MDBBtn color="info" outline size="sm">follow</MDBBtn>

      </div>
    );
  }
}

export default Follower;